import React from "react";
import { Route, BrowserRouter, Redirect } from "react-router-dom";
import { useStoreState, useStoreActions } from "easy-peasy";

import Auth from "../Auth/Auth";
import Settings from "./Settings/Settings";
import MyInfo from "./MyInfo/MyInfo";
import Reports from "./Reports/Reports";

function Main() {
  let setAuth = useStoreActions(action => action.auth.setAuth);
  return (
    <section id="dashboard" class="disp-flex">
      <BrowserRouter>
        <Route exact path="/" component={Auth} />
        <Route
          path="/logout"
          render={() => {
            localStorage.clear();
            setAuth(false);
            return <Redirect to="/settings" />;
          }}
        />
        <ProtectedRoute path="/settings" component={Settings} />
        <ProtectedRoute path="/myinfo" component={MyInfo} />
        <ProtectedRoute path="/activities" component={Reports} />
      </BrowserRouter>
    </section>
  );
}

function ProtectedRoute({ component: Component, ...rest }) {
  let isAuth = useStoreState(state => state.auth.isAuth);
  if (isAuth) {
    return <Route {...rest} component={Component} />;
  } else {
    return <Redirect to="/" />;
  }
}

export default Main;
