import React, { useState } from "react";
import CustomSelect from "../../../../../../UI/CustomSelect";

function ReportUpdateTask({
  tdesc,
  setTDesc,
  team,
  name,
  setName,
  dependancy,
  setDependancy,
  category,
  setCategory
}) {
  let [globalVisibility, setGlobalVisibility] = useState(true);
  return (
    <tr className="br-collapse">
      <td className="br-sm-blue fs-14 pd-8x pd-4y txt-align-center">
        <CustomSelect
          options={team}
          isStatic={true}
          placeholder={"Update"}
          value={name}
          selectValue={setName}
          globalVisibility={globalVisibility}
          setGlobalVisibility={setGlobalVisibility}
        />
      </td>
      <td className="br-sm-blue fs-14 pd-8x pd-4y txt-align-center">
        <input
          className="wi-100p bg-gray-trans br-none pd-4y pd-8x disp-ib"
          type="description"
          placeholder="Enter the task description"
          name="task-description"
          value={tdesc}
          onChange={e => setTDesc(e.target.value)}
        />
      </td>
      <td className="br-sm-blue fs-14 pd-8x pd-4y txt-align-center">N/A</td>
      <td className="br-sm-blue fs-14  bg-gray-trans txt-align-center">N/A</td>
      <td className="br-sm-blue fs-14 pd-8x pd-4y txt-align-center">N/A</td>
      <td className="br-sm-blue fs-14 bg-gray-trans txt-align-center">N/A</td>
      <td className="br-sm-blue fs-14 pd-8x pd-4y txt-align-center">
        <input
          className="wi-100p bg-gray-trans br-none pd-4y pd-8x disp-ib"
          type="number"
          placeholder="xx%"
          name="start-time"
          min={0}
          max={100}
          value={dependancy}
          onChange={e => setDependancy(e.target.value)}
        />
      </td>
      <td className="br-sm-blue fs-14 pd-8x pd-4y txt-align-center">N/A</td>
      <td className="br-sm-blue fs-14 bg-gray-trans txt-align-center">
        <CustomSelect
          options={[
            "Web development",
            "Web design",
            "Artificial Intelligence",
            "Learning"
          ]}
          isStatic={true}
          placeholder={"Update"}
          value={category}
          selectValue={setCategory}
          globalVisibility={globalVisibility}
          setGlobalVisibility={setGlobalVisibility}
        />
      </td>
      <td className="br-sm-blue fs-14 bg-gray-trans txt-align-center">N/A</td>
      <td className="br-sm-blue fs-14 bg-gray-trans txt-align-center">N/A</td>
      <td className="br-sm-blue fs-14 bg-gray-trans txt-align-center">N/A</td>
    </tr>
  );
}

export default ReportUpdateTask;
