import React, { useState, useEffect } from "react";

function CustomSelect({
  placeholder,
  options,
  selectValue,
  value,
  isStatic,
  globalVisibility,
  setGlobalVisibility
}) {
  let [isVisible, setVisible] = useState(false);

  return (
    <div
      className="pos-cont"
      onMouseEnter={e => {
        console.log("triggered", isVisible);
        setVisible(true);
      }}
      onMouseLeave={e => {
        console.log("triggered", isVisible);
        setVisible(false);
      }}
    >
      <input
        className="form-inp wi-80p cu-po"
        type="customselect"
        name="customselecct"
        id="fname"
        value={value}
        placeholder={placeholder}
      />
      <div className="pos-abs-top-left z-3">
        <ul>
          {isVisible
            ? !isStatic
              ? options
                  .filter(option => option.includes(value))
                  .map(correctOption => (
                    <li
                      key={correctOption}
                      onClick={e => {
                        selectValue(correctOption);
                        setVisible(!isVisible);
                        setGlobalVisibility(true);
                      }}
                      className="option-cust bg-light cu-po wi-100p"
                    >
                      {correctOption.replace(/\d+/, "")}
                    </li>
                  ))
              : options.map(correctOption => (
                  <li
                    onClick={e => {
                      selectValue(correctOption);
                      setVisible(!isVisible);
                      setGlobalVisibility(true);
                    }}
                    key={correctOption}
                    className="option-cust bg-light cu-po wi-100p"
                  >
                    {correctOption.replace(/\d+/, "")}
                  </li>
                ))
            : null}
        </ul>
      </div>
    </div>
  );
}

export default CustomSelect;
