import React, { useState, useEffect } from "react";
import ReportTable from "./ReportTable/ReportTable";
import { useStoreState, useStoreActions } from "easy-peasy";

function Report({ report }) {
  let [isVisible, setVisible] = useState(false);
  let isLoading = useStoreState(state => state.admin.isTaskLoading);

  let setTaskLoading = useStoreActions(action => action.admin.setTaskLoading);
  let [tasks, setTasks] = useState(report.tasks || []);
  let [milestone, setMilestone] = useState(
    report.tasks
      ? report.tasks[0]
        ? report.tasks[0].milestone
          ? report.tasks[0].milestone
          : "No milestone set yet"
        : "No milestone set yet"
      : "No milestone set yet"
  );
  let team = report.team;

  useEffect(() => {
    setTasks(report.tasks || []);
  }, [report.tasks]);

  return (
    <section className="pd-16 card bg-light report-inner mr-8t mr-16b  anim-trans">
      <h3 className="mr-8b">{report.activity_title || report.title}</h3>
      <p className="mr-8b tx-hier-2">{report.work || report.title}</p>
      <p className="fs-14 tx-hier-4">{report.activity_type}</p>

      {isVisible ? (
        <section className="pd-16 task-table-holder anim-trans">
          <h3 className="mr-8b fs-14">Todays report</h3>
          <p className="mr-8b">{milestone}</p>
          <ReportTable
            tasks={tasks}
            setTask={setTasks}
            suid={report.suid}
            team={team}
            activityid={report.activityid}
            milestone={milestone}
            setMilestone={setMilestone}
            setVisible={setVisible}
            isLoading={isLoading}
          />
        </section>
      ) : null}
      <section id="imp-sec" className="disp-flex fl-jc-fe">
        {/* <button className="btn mr-8t mr-12r">Project Completed</button> */}
        <button
          className="btn mr-8t"
          onClick={e => {
            if (!isVisible) setTaskLoading(true);
            setVisible(!isVisible);
          }}
        >
          View {isVisible ? "Less" : "More"}
        </button>
      </section>
    </section>
  );
}

export default Report;
