import React from "react";
import MainNav from "../MainNav/MainNav";
import UpdateSkill from "./UpdateSkills/UpdateSkills";
import UpdateQualification from "./UpdateQualifications/UpdateQualification";
import Skills from "./Skills/Skills";
import Qualifications from "./Qualifications/Qualifications";
import { useStoreState } from "easy-peasy";

function MyInfo() {
  let fname = useStoreState(state => state.profile.fname);
  let lname = useStoreState(state => state.profile.lname);
  let email = useStoreState(state => state.profile.email);
  let address_line1 = useStoreState(state => state.profile.address_line1);
  let address_line2 = useStoreState(state => state.profile.address_line2);

  let dist = useStoreState(state => state.profile.dist);
  let state = useStoreState(state => state.profile.state);
  let branch = useStoreState(state => state.profile.branch);
  let aboutme = useStoreState(state => state.profile.aboutme);
  let phone = useStoreState(state => state.profile.phone);
  let skills = useStoreState(state => state.profile.skills);
  let qualification = useStoreState(state => state.profile.qualification);
  let pin = useStoreState(state => state.profile.pin);

  return (
    <>
      <MainNav />
      <section id="resume" className="flex-basis-70-l">
        <header id="resume-header" className="pd-16 mr-16 flex-basis-50-l">
          <h2 className="fs-24 bold-josef">My information</h2>
        </header>
        <section className="card mr-16 pd-32 disp-flex bg-light">
          <section className="flex-basis-20-l disp-flex fl-dir-col">
            <h2 className="mr-16b fs-24 bold-josef">
              {fname} <br /> {lname}
            </h2>
            <p className="fs-18 mr-32b">{branch}</p>
            <h3 className="fs-18 mr-16b tx-hier-3">
              Contact <br /> information
            </h3>
            <p className="tx-hier-3 fs-16 mr-8b">{email}</p>
            <p className="tx-hier-3 fs-16 mr-32b">{phone}</p>
            <h3 className="fs-18 mr-16b tx-hier-3">Address</h3>
            <p className="tx-hier-3 fs-16 mr-8b">{address_line1}</p>
            <p className="tx-hier-3 fs-16 mr-8b">{address_line2}</p>
            <p className="tx-hier-3 fs-16 mr-8b">{pin}</p>
            <p className="tx-hier-3 fs-16 mr-32b">
              {dist}, {state}
            </p>
          </section>
          <section className="flex-basis-50-l">
            <h2 className="fs-24 mr-16b  bold-josef">About me</h2>
            <p className="fs-20 lh-16 mr-32b">{aboutme}</p>
            <h2 className="fs-24 mr-16b  bold-josef">
              Educational qualification
            </h2>
            <Qualifications qualification={qualification} />
            <UpdateQualification />
            <h2 className="fs-24 mr-16b  bold-josef mr-16t">
              Skills (technical/soft)
            </h2>
            <Skills skills={skills} />
            <UpdateSkill />
          </section>
        </section>
      </section>
    </>
  );
}

export default MyInfo;
