import React, { useState, useEffect } from "react";
import ReportUpdateTask from "./ReportTableRow/ReportUpdateTask/ReportUpdateTask";

import ReportTableRow from "./ReportTableRow/ReportTableRow";
import ReportUpdateMilestone from "./ReportTableRow/ReportUpdateMilestone/ReportUpdateMilestone";
import { useStoreState, useStoreActions } from "easy-peasy";
import ReportTableHeader from "./ReportTableRow/ReportTableHeader/ReportTableHeader";

function ReportTable({
  tasks,
  suid,
  team = [],
  activityid,
  milestone,
  setMilestone,
  setVisible,
  isLoading
}) {
  let setActivityTasks = useStoreActions(
    action => action.admin.fetchActivityTask
  );
  let [isAssignTaskVisible, setAssignTaskVisible] = useState(false);
  let [newTasks, setNTask] = useState([]);
  let [custteam, setTeam] = useState(
    team.filter(
      member => !(tasks && tasks.some(task => task.user_id === member.user_id))
    )
  );
  let setTasks = useStoreActions(action => action.admin.setTasks);
  let [name, setName] = useState("");

  let [dependancy, setDependancy] = useState("");

  let [category, setCategory] = useState("");

  let [tdesc, setTDesc] = useState("");

  useEffect(() => {
    if (isLoading) setActivityTasks(activityid);
  }, [isLoading]);

  function setCorrectTask() {
    setAssignTaskVisible(false);
    setTeam(team.filter(teamMember => !name.includes(teamMember.user_id)));

    setNTask([
      ...newTasks,
      {
        user_id: parseInt(name.match(/\d+/)),
        name: name.replace(/\d+/),
        task_description: tdesc,
        dependancy,
        category
      }
    ]);
  }

  let user_id = useStoreState(state => state.profile.user_id);
  return (
    <>
      <table id="task-table" className="br-collapse anim-down">
        <thead>
          {suid === user_id && milestone === "No milestone set yet" ? (
            <ReportUpdateMilestone
              milestone={milestone}
              setMilestone={setMilestone}
            />
          ) : null}
          {(tasks && tasks.length) || (newTasks && newTasks.length) ? (
            <ReportTableHeader />
          ) : null}
        </thead>
        <tbody className="card">
          {(tasks && tasks.length) || (newTasks && newTasks.length) ? (
            [...tasks, ...newTasks].map(task => (
              <ReportTableRow setVisible={setVisible} task={task} />
            ))
          ) : (
            <tr className="br-collapse">
              <td className="br-sm-blue fs-14 pd-8x pd-4y lh-16" colspan={12}>
                {isLoading ? (
                  <>
                    <div className="wi-100p txt-align-center">
                      <img className="wi-30" src="loading.gif" />
                      <p>loading...</p>
                    </div>
                  </>
                ) : null}
                <p>Tasks are not yet assigned</p>
              </td>
            </tr>
          )}
          {!((tasks && tasks.length) || newTasks.length) &&
          isAssignTaskVisible ? (
            <ReportTableHeader />
          ) : null}
          {isAssignTaskVisible && user_id === suid ? (
            <ReportUpdateTask
              team={custteam.map(
                member => member.fname + " " + member.lname + member.user_id
              )}
              tdesc={tdesc}
              setTDesc={setTDesc}
              name={name}
              setName={setName}
              dependancy={dependancy}
              setDependancy={setDependancy}
              category={category}
              setCategory={setCategory}
            />
          ) : null}
        </tbody>
      </table>
      {isAssignTaskVisible ? (
        <>
          <button
            onClick={e => {
              setAssignTaskVisible(!isAssignTaskVisible);
            }}
            className="btn mr-16"
          >
            Cancel
          </button>
        </>
      ) : null}
      {newTasks && newTasks.length && suid === user_id ? (
        <button
          className="blk btn  mr-16t mr-12r"
          onClick={e => {
            setVisible(false);
            setTasks({ user_id: newTasks, milestone, activityid, suid });
          }}
        >
          Submit report
        </button>
      ) : null}
      {suid === user_id &&
      !isAssignTaskVisible &&
      !team.every(member =>
        [...newTasks, ...(tasks.length ? tasks : [])].some(
          task => task.user_id === member.user_id
        )
      ) ? (
        <button
          onClick={e => {
            setAssignTaskVisible(!isAssignTaskVisible);
          }}
          className="blk btn mr-16t mr-12r"
        >
          Assign new task
        </button>
      ) : null}
      {isAssignTaskVisible ? (
        <>
          <button
            className="blk btn  mr-16t mr-12r"
            onClick={e => {
              setCorrectTask();
            }}
          >
            Submit Changes
          </button>
        </>
      ) : null}
    </>
  );
}

export default ReportTable;
