import React from "react";

function ReportUpdateMilestone({ setMilestone }) {
  return (
    <tr className="br-collapse">
      <td className="br-sm-blue fs-14 pd-8x pd-4y lh-16" colSpan={7}>
        <input
          id="milestone"
          className="wi-100p bg-gray-trans br-none pd-4y pd-8x disp-ib mr-8b"
          type="text"
          placeholder="Enter the current mile stone"
          name="milestone"
        />
        <button
          className="disp-ib blk btn mr-4b"
          onClick={e =>
            setMilestone(document.getElementById("milestone").value)
          }
        >
          New milestone
        </button>
      </td>
    </tr>
  );
}

export default ReportUpdateMilestone;
