import {
  action,
  thunk
} from "easy-peasy";
import axios from "axios";

export default {
  fname: "",
  lname: "",
  type: "U",
  dob: "",
  sex: "",
  phone: "",
  sem: "",
  domainId: "",
  address_line1: "",
  address_line2: "",
  city: "",
  dist: "",
  state: "",
  pin: "",
  branch: "",
  aboutMe: "",
  email: "",
  skills: [],
  isDone: false,
  setDone: action((state, payload) => {
    state["isDone"] = payload;
  }),
  setProfile: action((state, payload) => {
    for (let i in payload) state[i] = payload[i];
  }),
  attendance: "",
  aboutme: "",
  updateProfile: thunk(async (action, payload) => {
    try {
      let profile = await axios.post(
        "/profile",
        payload, {
          headers: {
            "x-auth-token": localStorage.getItem("token")
          }
        }
      );
      profile = profile.data;
      action.setProfile(profile);
      action.setDone(true);
      setTimeout(() => action.setDone(false), 3000);
    } catch (err) {
      localStorage.removeItem("token");

    }
  }),
  updateSkills: thunk(async (action, payload) => {
    try {
      let skill = await axios.post(
        "/profile/skills",
        payload, {
          headers: {
            "x-auth-token": localStorage.getItem("token")
          }
        }
      );
      action.setProfile(skill.data);
    } catch (err) {

    }
  }),
  updateQualification: thunk(async (action, payload) => {
    try {
      let qualification = await axios.post(
        "/profile/qualification",
        payload, {
          headers: {
            "x-auth-token": localStorage.getItem("token")
          }
        }
      );

      action.setProfile(qualification.data);
    } catch (err) {

    }
  })
};